.movies-list {
  width: 1200px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  &__item {
    position: relative;
    width: 23%;
    height: auto;
    padding: 10px;
    background-color: #fff;
    margin: 1%;
    transition: transform 0.5s;
    box-sizing: border-box;

    &:hover {
      transform: translateY(-10px);
    }

    img {
      width: 100%;
      margin-bottom: -4px;
    }

    h3 {
      padding: 0;
      margin: 14px 0 0 0;
      font-size: 16px;
      font-weight: 700;
      text-align: left;
    }

    p {
      padding: 0;
      margin: 7px 0 0 0;
      text-align: left;
      font-size: 16px;
    }
  }

  &__wrapper {
    position: relative;
  }

  &__rating {
    position: absolute;
    background: #fff;
    top: 17px;
    right: 13px;
    padding: 1px 5px;
    box-shadow: 0px 0px 10px rgb(0 0 0 / 25%);
  }
}

.no-found {
  margin-top: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;

  h3 {
    width: 100%;
    margin-bottom: 20px;
  }

  img {
    max-width: 200px;
  }
}



@media (max-width: 768px) {
  .movies-list {
    margin: 40px 10px;
    width: 100%;


    &__item {
      position: relative;
      width: 48%;
      height: auto;
      padding: 20px 10px;
      box-sizing: border-box;
    }
  }

}

